import React, { Component } from 'react';
import './App.css';
import marked from 'marked';

const initialState = `
  This is a paragraph

  **Bold text**
  
  > Block quote

  # Heading
  ## Heading 2
  ### Heading 3

  - item 
  

  [MarkedJS](https://marked.js.org/)


  This is a inline \`<div></div>\`

  This is a block of code

  \`\`\`
  const a = 2;
  const b = 2;
  let c = a+b;
  \`\`\`


  ![React Logo ](data:image/svg+xml;base64,)
`;

class App extends Component {
  state = {
    text: initialState
  }

handleChange = (e) => {
  this.setState({
    text: e.target.value
  })
}
  render(){
    const { text } = this.state;
    const markdown = marked(text, { breaks:true });
    return (
      <div>
        <h2 className="text-center mt-4">Convert your Markdown</h2>
        <div className = 'row'>
          <div className = 'col-6'>
            <label class="form-label">Enter your markdown:</label>
            <textarea className="form-control rounded p-2" name="" id="editor" cols="30" rows="10" value={ text } onChange={this.handleChange}></textarea>
          </div>
          <div className = 'col-6' >
          <label class="form-label">Result:</label>
          <div className = "preview rounded p-2" dangerouslySetInnerHTML={{__html: markdown}} id="preview"/>
          </div>
        </div>
      </div>
    )
  }
}

export default App;
